import {FETCH_QUIZ_SUCCESS, QUIZ_NEXT_QUESTION, 
  FINISH_QUIZ, QUIZ_SET_STATE,  FETCH_QUIZES_ERROR, QUIZ_RETRY,
  FETCH_QUIZES_START, FETCH_QUIZES_SUCCESS} from '../actions/actionTypes';

const initialState = {
  quizes: [],
  loading: false,
  error: null,
  results: {},
  isFinished: false,
  activeQuestion: 0,
  answerState: null,
  quiz: null
}

export default function quizReducer(state = initialState, action) {
  switch (action.type) {
    case FETCH_QUIZES_START:
      return {
        ...state, loading: true
      }
    case FETCH_QUIZES_SUCCESS:
      return {
        ...state, loading: false, quizes: action.quizes
      }
    case FETCH_QUIZES_ERROR:
      return {
        ...state, loading: false, error: action.error
      }
    case FETCH_QUIZ_SUCCESS:
      return {
        ...state, loading: false, quiz: action.quiz
      }
    case QUIZ_SET_STATE:
      return {
        ...state, answerState: action.answerState, results:action.results
      }
    case FINISH_QUIZ:
      return {
        ...state, isFinished: true
      }
      case  QUIZ_NEXT_QUESTION:
        return {
          ...state, answerState:null, activeQuestion: action.number
        }
      case QUIZ_RETRY:
        return {
          ...state, 
          activeQuestion: 0,
          answerState: null,
          isFinished: false,
          results: {}
        }
    default:
      return state
  }
}


